	import helper from '../../common/helper.js';
	export default {
		data() {
			return {
				phone: '',
				tipsCode: "获取验证码",
				code: '',
				codes: '',
				codestr: '',
				timeNum: 60,
				clikType: false,
				timeOut: 60,				
			};
		},
		methods:{
			// 获取验证码
			openCodeDialog: function() {
				var that = this
				if (that.phone == '') {
					uni.showToast({
						icon: 'none',
						title: '请输入你的手机号码'
					});
					return;
				} else if (!helper.checkmobile(that.phone)) {
					uni.showToast({
						icon: 'none',
						title: '请输入正确的手机号码'
					});
					return;
				} else if (!that.clikType) {
					that.getTime();
					that.codestr = helper.MathRand(4);
					that.clikType = true;
					uni.request({
						url:helper.websiteUrl+'send/sendcode',
						method: 'GET',
						data: {
							loginMark: helper.getloginMark(),
							token: '',
							data: '{"phone": "' + that.phone + '"}'
						},
						success: res => {
							if (res.data.code == 200) {
								
								this.codes = res.data.info;
							} else {
								uni.showToast({
									icon: 'none',
									title: '获取验证码失败,请重试'
								});
							}
						},
						fail: () => {
							uni.hideLoading();
							uni.showToast({
								icon: 'none',
								title: '网络异常,请稍后重试'
							});
						}
					});
				}
			},
			// 验证码倒计时
			getTime() {
				var that = this
				var timer = setInterval(function() {
					that.timeNum--;
					if (that.timeNum <= 0) {
						that.tipsCode = "重新获取验证码";
						that.timeNum = that.timeOut;
						that.clikType = false;
						clearInterval(timer);
					} else {
						that.tipsCode = "重新发送" + that.timeNum + "秒";
					}
				}, 1000)
			},
			formSubmit: function(e) {
				if (!helper.checkmobile(this.phone)) {
					uni.showToast({
						icon: 'none',
						title: '请输入正确的手机号码'
					});
					return;
				}
				if (this.code == "" || this.code != this.codes || this.codes !=
					this.code) {
					uni.showToast({
						icon: 'none',
						title: '验证码错误'
					});
					return;
				}
				this.userdata = e.detail.value
				uni.request({
					url:helper.websiteUrl+'merchantcenter/checkphone' ,
					method: 'GET',
					data:helper.postdata({
						"type":'2',
						"phone": this.userdata.phone,
						"code": this.userdata.code,
					}),
					success: res => {
						if (res.data.code == 200) {
							var userinfo = res.data.data;
							helper.setstate(userinfo.id);
 							uni.redirectTo({
 								url: 'MerResetpass2'
 							});
						} else {
							uni.showToast({
								icon: 'none',
								title: '该手机号尚未注册'
							});
						}
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络异常,请稍后重试'
						});
					}
				});
			}
		}
	}
